.header {

    padding: rem(25);

    & a {
        color: #575757;
    }



    & .container {
        display: flex;
        align-items: center;

        &>div {
            margin-right: rem(30);

            &:last-child {
                margin-right: rem(0);
            }
        }
    }

    &__logo {
        padding-right: 2%;
    }

    &__info {
        @media screen and (max-width: $w1440) {
            margin-left: auto;
        }
    }

    &__menu {
        @media screen and (min-width: $w1440) {
            display: none;
        }

        position: absolute;
        right:0;
        top: 0;
        z-index: 12;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;

        & .burger i {
            background: #fff !important;
        }
    }

    &__nav {

        flex-grow: 1;

        @media screen and (max-width: $w1440) {
            position: absolute;
            margin: 0;
            padding: 0;
            right: 0;
            top: 0;
            width: 80px;
            height: 80px;
            background-color: $lgreen;
            border-bottom-left-radius: 100%;
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
            animation: not-checked-anim .2s both;
            transition: .2s;
            margin-right: 0 !important;
            z-index: 10;

            &.open {
                animation: checked-anim 0.8s ease both;


                & nav {
                    width: 100vw;
                    height: 100vh;
                    max-width: 100vw;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    opacity: 1;

                }

                & ul {
                    display: flex;
                    flex-direction: column;
                    max-width: 100vw;

                    & a {
                        color: #fff;
                        @include font-size-16();
                        text-align: center;
                        display: flex;
                        justify-content: center;
                        padding: 20px 0;

                        & .btn {
                            @include font-size-16();
                            font-weight: 500;

                            background-color: #fff;
                            color: #000;
                        }
                    }
                }

                & li {
                    display: block;
                    width: 100vw;
                    margin-right: 0;
                    opacity: 1;
                    transition: opacity 0.3s ease 0.6s;

                }

            }


        }

        & nav {

            @media screen and (max-width: $w1440) {
                opacity: 0;
            }

            & li {

                &.menu {
                    & a {
                        border: 1px solid $lgreen;
                        border-radius: 30px
                    }
                }

                & a {
                    display: flex;
                    align-items: center;
                    height: rem(55);
                    padding-top: 0.35em;
                    padding-left: 2em;
                    padding-right: 2em;

                    & svg {
                        margin-right: rem(10);
                        margin-top: -0.35em;
                    }


                }
            }

            & ul {
                display: flex;
                align-items: center;
            }
        }
    }

    &__worktime {
        padding-top: rem(10);
        margin-right: rem(22);
        line-height: 100%;
        display: flex;
        align-items: center;
        white-space: nowrap;




        & span {
            border-bottom: 1px dashed #575757;

        }

        &::before {
            content: '';
            width: 0.33em;
            min-width: 0.33em;
            height: 0.33em;

            background-color: $lgreen;
            border-radius: 50%;
            display: inline-block;
            margin-right: rem(7);
            margin-top: -0.35em;

        }
    }


    &__signup {
        @media screen and (max-width: $w760) {
            display: none;
        }
    }



}

.swiper-wrapper,
.swiper-slide,
.swiper-container {
    height: 100%;
}

@keyframes checked-anim {
    50% {
        width: 1440px;
        height: 1440px;
    }

    100% {
        width: 100%;
        height: 100%;
        border-radius: 0;
    }
}

@keyframes not-checked-anim {
    0% {
        width: 1440px;
        height: 1440px;
    }
}

.header-phone {

    &__active {

        text-align: right;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        & a {
            font-family: New_Channel_Font;
            font-size: rem(20);
            line-height: 100%;
            color: #000000;
        }

        & svg {
            margin-left: rem(10);
            width: rem(12);
            min-width: rem(12);
        }
    }

    &__list {
        display: none;
    }
}

.main-slider {

    position: relative;

    @media screen and (max-width: $w760) {
        padding-top: rem(50);
    }

    &__bg {
        background: linear-gradient(104.26deg, #99B6C6 -4.69%, rgba(153, 182, 198, 0) 60.13%), #BCD8E5;
        padding-top: 33%;
        transition: 2s ease;

        @media screen and (max-width: $w760) {
            @include layer(1);
            padding-top: 0%;
        }
    }

    &__content {
        @include layer(1);

        @media screen and (max-width: $w760) {
            position: relative;
            z-index: 2;

        }

        display: flex;
        align-items: flex-end;

        .swiper-wrapper {
            @media screen and (max-width: $w760) {
                align-items: flex-end;
            }
        }
    }
}

.main-slider-slide {

    @include row-flex;
    max-width: 1190px;
    margin: 0 auto;

    display: flex;
    align-items: center;
    height: 100%;

    &__text {
        @include col;
        @include size-w760(6);

        display: flex;
        flex-direction: column;
        justify-content: center
    }

    &__image {
        @include col;
        @include size-w760(6);
    }

    &>div {
        height: 100%;

        @media screen and (max-width: $w760) {
            width: 100%;
        }
    }

    &__image {
        & .bgimage {
            height: 100%;
            @include bgimage-contain;
            background-position: center bottom;

            @media screen and (max-width: $w760) {
                padding-top: 50%;
            }

            @media screen and (max-width: $w580) {
                padding-top: 80%;
            }
        }
    }

    &__title {
        @include section-title;

        transform: translateY(10px);
        opacity: 0;
        transition: 0.5s ease 0.3s;
    }

    &__subtitle {
        @include font-size-16();
        line-height: 170%;
        padding-top: rem(30);

        transform: translateY(10px);
        opacity: 0;
        transition: 0.5s ease 0.5s;

        @media screen and (max-width: $w940) {
            font-size: rem(15);
        }
    }


}

.swiper-slide-active {

    & .main-slider-slide__title,
    & .main-slider-slide__subtitle {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes swiper-progress-bar {
    from {
        width: 0%;
    }

    to {
        width: 100%;
    }
}

.swiper-main-slider {

    &__dots {
        position: absolute;
        left: 0;
        right: 0;
        width: 100%;
        display: flex;
        align-items: center;
        bottom: rem(25);
        justify-content: center;
        z-index: 3;

        & .swiper-pagination-bullet {
            width: rem(12);
            min-width: rem(12);
            height: rem(12);
            background-color: #fff;
            opacity: 1;
            border-radius: 15px;
            margin: 0 rem(5);
            border: 4px solid #fff;
            transition: 0.3s ease;
            position: relative;

            & span {
                width: 0%;
                background-color: #fff;
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                border-radius: 5px;


                transition-timing-function: ease;
                transition-property: width;
            }
        }

        & .swiper-pagination-bullet-active {
            width: rem(50);
            min-width: rem(12);
            background-color: #D9D9D9;
            position: relative;

            & span {
                width: 100%;
                background-color: $lgreen;

                animation-name: swiper-progress-bar;
                animation-timing-function: ease;
            }
        }
    }

    &__nav {
        position: absolute;
        top: 50%;
        height: 0px;
        left: 0;
        right: 0;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        z-index: 3;

        &-prev,
        &-next {
            width: rem(50);
            height: rem(55);
            background: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;

            & svg {
                fill: $lgreen;
            }
        }

        &-prev {
            border-radius: 0 30px 30px 0;
        }

        &-next {
            border-radius: 30px 0 0 30px;
        }

        & .swiper-button-disabled {
            & svg {
                fill: #ccc;
            }
        }

    }

}

.section-our-services,
.section-our-doctor {
    padding-top: rem(90);
    padding-bottom: rem(90);

    @media screen and (max-width: $w760) {
        padding-bottom: rem(30);
        padding-top: rem(30);
    }
}

.our-services,
.our-doctor {
    @include row-flex;

    &__left {
        @include col;
        @include size-w1024(3);

        @include size(12);

        @media screen and (max-width: $w1024) {
            margin-bottom: rem(60)
        }
    }

    &__right {
        @include col;
        @include size-w1024(9);
        @include size(12)
    }

    &__title {
        @include section-title();
    }

    &__subtitle {
        @include section-subtitle();
    }

    &__btn {
        padding-top: rem(25)
    }
}

.our-services-list,
.our-doctor-list {
    @include row-flex;
}

.our-services-item {
    @include col;
    @include size-w1024(4);
    @include size-w480(6);
    @include size(12);


    &__wrp {
        margin-bottom: rem(60);
    }

    &__image {
        & .bgimage {
            padding-top: 70%;
            @include bgimage-cover;

            &:hover {
                transition: 0.3s cubic-bezier(.18, .89, .32, 1.28);
                transform: translateY(-5px);
            }
        }


    }

    &__title {
        @include block-title();

        padding-top: rem(15);
    }

    &__desc {
        color: #575757;
        @include font-size-14();
        line-height: 170%;

        padding-top: rem(15);
    }

    &__btn {

        padding-top: rem(15);

        & a {
            font-family: New_Channel_Font;
            @include font-size-16();
            line-height: 16px;
            text-transform: uppercase;
            color: #40857D;
            padding-bottom: rem(8);
            border-bottom: 1px solid #B9B9B9;
        }
    }
}

.our-doctor-item {
    @include col;
    @include size-w1024(4);
    @include size-w480(6);
    @include size(12);



    &__image {
        & .bgimage {
            padding-top: 110%;
            @include bgimage-cover;
        }
    }

    &__wrp {
        margin-bottom: rem(60);
    }

    &__title {
        @include block-title();

        padding-top: rem(15);
    }

    &__desc {
        color: #575757;
        @include font-size-14();
        line-height: 170%;

        padding-top: rem(15);
    }

    &__btn {

        padding-top: rem(15);

        & a {
            font-family: New_Channel_Font;
            @include font-size-16();
            line-height: 16px;
            text-transform: uppercase;
            color: #40857D;
            padding-bottom: rem(8);
            border-bottom: 1px solid #B9B9B9;
        }
    }
}

.section-news {
    padding-top: rem(70);
    padding-bottom: rem(70);

    @media screen and (max-width: $w760) {
        padding-top: rem(30);
        padding-bottom: rem(30);
    }
}

.all-news {
    @media screen and (max-width: $w760) {
        padding-top: rem(35);
    }
}

.news {
    &__list {
        @include row-flex;
    }

    &__item {
        @include col;
        @include size-w1024(3);
        @include size-w940(4);
        @include size-w480(6);

        margin-bottom: rem(50);
    }

    &__item-tab {
        display: none;

        &.active {
            display: block;
        }
    }

    &__tabs {
        display: flex;
        justify-content: space-between;
        padding-bottom: 80px;

        @media screen and (max-width: $w760) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        & ul {
            display: flex;
            align-items: center;
        }

        & li {
            @include section-title();
            margin-right: rem(45);
            color: #575757;
            border-bottom: 1px dashed #575757;
            cursor: pointer;

            &:hover {
                opacity: 0.9;
            }

            &:last-child {
                margin-right: rem(0);
            }

            &.active {
                color: $lgreen;
                border-bottom: 1px dashed transparent;
            }
        }
    }
}

.tab-content {
    display: none;
    -webkit-animation: .4 slide-up;
    animation: .4s slide-up;
}

.about {
    &__wrp {
        & .swiper-slide {
            &:first-child {
                & .about-slide__left {
                    & .about-slide__image {
                        margin-top: rem(40);
                        max-width: 75%
                    }
                }
            }

            &:last-child {
                & .about-slide__right {

                    display: flex;

                    & .about-slide__image {
                        margin-top: auto;
                        max-width: 75%;
                        margin-left: auto;
                        margin-bottom: 0;
                        width: 100%
                    }
                }
            }
        }
    }
}

.about-slide {
    @include row-flex();

    padding: 0 rem(110);

    @media screen and (max-width: $w1440) {
        padding: 0 rem(30);

    }



    &__left {
        @include col;
        @include size-w760(6);
        @include size(12);
    }

    &__right {
        @include col;
        @include size-w760(6);
        @include size(12);

        @media screen and (max-width: $w760) {
            padding-top: rem(60);
        }
    }

    &__image {
        & .bgimage {
            @include box-square;
            background-color: #ddd;
            @include bgimage-contain;
        }
    }

    &__title {
        @include section-title();
    }

    &__subtitle {
        @include section-subtitle();

        & p {
            margin-bottom: rem(20);
        }
    }

    &__btn {
        padding-top: rem(35);
    }
}